/* stylelint-disable color-function-notation */
/* stylelint-disable alpha-value-notation */
.breadcrumbs {
  padding: 24px 0;

  &__list {
    display: flex;
    align-items: flex-start;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }

    &:first-child .breadcrumbs__link::after {
      content: "/";
      margin-left: 20px;
      width: 20px;
      height: 20pxy;
    }

    &:last-child .breadcrumbs__link {
      color: rgba(53, 38, 38, 0.48);
    }
  }

  &__link {
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--el-color);
    }

    @include small-tablet {
      font-size: 14px;
    }
  }
}
