/* stylelint-disable no-extra-semicolons */
.hero {
  position: relative;
  padding: 180px 0 294px;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    z-index: -1;
    display: block;
    width: 1920px;
    height: 100%;
    background-image: url(../img/hero-bg.jpg);
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    transform: translateX(-50%);
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    background-color: rgb(84 15 53 / 36%);
    inset: 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 48px;
    line-height: 130%;
    color: var(--light-color);

    & br {
      display: none;

      @include tablet {
        display: block;
      }
    }

    @include small-tablet {
      margin-bottom: 12px;
      font-size: 28px;
    }
  }

  &__text {
    margin: 0;
    max-width: 700px;
    font-weight: 700;
    font-size: 24px;
    line-height: 130%;
    color: var(--light-color);

    @include small-tablet {
      font-size: 16px;
    }
  }

  @include small-tablet {
    padding: 112px 0 86px;
  }
}
