/* stylelint-disable scss/selector-no-redundant-nesting-selector */
.footer {
  position: relative;
  background-color: var(--bg-color);

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 34px;
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--el-color);

    @include small-tablet {
      bottom: 28%;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 32px 0 50px;

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__logo {
    flex: 1 0 auto;

    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__contacts {
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-left: 40px;

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      margin-left: 0;
    }
  }

  &__contact {
    margin-bottom: 20px;

    &:not(:first-child) {
      @include small-tablet {
        margin-left: 0;
      }
    }

    &:not(:last-child) {
      @include small-tablet {
        margin-bottom: 40px;
      }
    }
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px 0 5px;

    @include small-tablet {
      align-items: center;
    }
  }

  &__bottom-text {
    display: inline-flex;
    align-items: center;
    margin: 0;
    font-weight: 500;
    font-size: 10px;
    color: var(--light-color);

    & svg {
      margin-left: 12px;
      width: 24px;
      height: 24px;
      fill: var(--light-color);
    }

    &:nth-child(1) {
      @include small-tablet {
        order: 0;
      }
    }

    &:nth-child(2) {
      @include small-tablet {
        order: -2;
        width: 100%;
      }
    }

    &:nth-child(3) {
      &:hover {
        color: var(--el-color);
      }

      @include small-tablet {
        order: -1;
        width: 100%;
      }
    }

    &:nth-child(4) {
      &:hover {
        color: var(--el-color);
      }

      &:hover svg {
        fill: var(--el-color);
      }

      @include small-tablet {
        order: 0;
      }
    }

    @include small-tablet {
      justify-content: center;
      margin-bottom: 20px;
      text-align: center;
    }
  }
}
