/* stylelint-disable alpha-value-notation */
/* stylelint-disable color-function-notation */
.page-contact {
  padding-bottom: 60px;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include small-tablet {
      justify-content: space-evenly;
    }

    @include mobile {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__contact {
    &:not(:last-child) {
      margin-right: 20px;

      @include small-tablet {
        margin-bottom: 30px;
      }
    }
  }

  &__address {
    display: flex;
    font-style: normal;
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__text {
    font-weight: 700;
    font-size: 24px;
    line-height: 130%;
    color: var(--text-color);

    @include small-tablet {
      font-size: 20px;
    }
  }

  &__text--weight {
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: var(--text-color);

    @include small-tablet {
      font-size: 15px;
    }
  }

  &__caption {
    display: flex;
    align-items: flex-start;
    font-weight: 700;
    font-size: 15px;
    line-height: 130%;
    color: rgba(53, 38, 38, 0.42);
  }

  &__link {
    padding-left: 40px;
    font-weight: 700;
    font-size: 24px;
    line-height: 130%;
    color: var(--text-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--el-color);
    }

    @include small-tablet {
      font-size: 20px;
    }
  }

  &__svg {
    margin-right: 16px;
    width: 24px;
    height: 24px;
    fill: var(--el-color);
  }
}
