/* stylelint-disable alpha-value-notation */
/* stylelint-disable no-extra-semicolons */
/* stylelint-disable color-function-notation */
.video {
  position: relative;
  overflow: hidden;
  margin-bottom: 100px;
  width: 100%;
  height: 710px;
}

.video iframe,
.video img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.video__play {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
  width: 128px;
  height: 128px;
  color: var(--color-light);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

.video__play:hover {
  opacity: 0.7;
}

.video__play--hidden {
  display: none;
}

.video--visible {
  position: relative;
}

