/* stylelint-disable declaration-block-semicolon-newline-after */
/* stylelint-disable color-function-notation */
.work {
  padding-bottom: 100px;

  &__container {
    position: relative;
  }
}

.work-slider {
  padding-bottom: 75px;

  &__btns {
    position: absolute;
    right: 0;
    top: 0;

    @include small-tablet {
      display: none;
    }
  }

  &__btn {
    width: 36px;
    height: 36px;

    & svg {
      width: 100%;
      height: 100%;
      fill: var(--bg-color);
    }
  }

  &__prev {
    margin-right: 48px;
  }

  &__pag {
    & .swiper-pagination-bullet {
      border-radius: 0;
      width: 16px;
      height: 16px;
      background-color: rgba(205, 107, 62, 0.57);
      opacity: 1;
    }

    & .swiper-pagination-bullet-active {
      position: relative;
      top: 2px;
      border: 5px solid var(--bg-color);
      width: 20px;
      height: 20px;
      background-color: var(--el-color);
    }
  }

  &__text {
    margin: 20px 0;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    color: rgba(84, 15, 53, 49%);
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;
    color: #352626;
  }

  @include tablet {
    padding-right: 20px;
    padding-left: 20px;
  }
}
