.activity {
  padding: 90px 0 70px;

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 6 span;
    }

    @include small-tablet {
      grid-column: 4 span;
      padding: 70px 0 100px;
    }
  }
}

.activity-card {
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: 100%;
  box-shadow: 4px 4px 20px 0 rgb(84 15 53 / 7%);
  background-color: var(--light-color);

  &__image {
    @include tablet {
      margin: 0 auto;
    }
  }

  &__text {
    margin: 24px 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;
    color: var(--text-color);

    @include tablet {
      text-align: center;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__link {
    display: inline-block;
    margin-top: auto;
    border: 1px solid transparent;
    padding: 14px;
    width: 100%;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-align: center;
    color: var(--light-color);
    background-color: var(--el-color);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border 0.3s ease-in-out;

    &:hover {
      border: 1px solid var(--el-color);
      color: var(--el-color);
      background-color: var(--light-color);
    }
  }
}
