.page-hero {
  padding: 40px 0 100px;

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 48px;
    line-height: 130%;
    color: var(--text-color);

    @include small-tablet {
      font-size: 32px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: var(--text-color);

    @include small-tablet {
      font-size: 15px;
    }
  }

  @include small-tablet {
    padding: 40px 0 80px;
  }
}
