.burger {
  @include burger;

  z-index: 20;
  display: none;

  @include tablet {
    display: block;
    margin-left: 40px;
    color: var(--el-color);
  }

  @include mobile {
    margin-left: 16px;
  }
}

.nav__list {
  &.menu--active {
    position: absolute;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--bg-color);
    inset: 0;

    .nav__item:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  @include tablet {
    display: none;
  }
}

.burger-wrap {
  position: relative;
  display: none;
  margin-top: 20px;
  width: 48px;
  height: 48px;

  &::before {
    content: "";
    position: absolute;
    left: -6px;
    top: -12px;
    z-index: 10;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(0 177 201 / 8%);
  }

  @include tablet {
    display: block;
  }
}
