/* stylelint-disable color-function-notation */
.header {
  position: relative;
  background-color: var(--bg-color);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 48px;
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--el-color);

    @include tablet {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--el-color);

    @include tablet {
      bottom: inherit;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;

    @include tablet {
      padding: 16px 0;
    }
  }

  &__link {
    @include small-tablet {
      display: none;
    }
  }

  &__caption {
    @include small-tablet {
      font-size: 0;
    }
  }

  &__svg {
    @include small-tablet {
      margin-right: 0;
      width: 40px;
      height: 40px;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;

    @include tablet {
      padding: 0;
    }
  }

  @include tablet {
    position: inherit;
  }
}

.nav {
  width: 100%;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--el-color);
    }
  }
}
