.contacts {
  display: flex;
  align-items: center;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
  }

  &__contacts {
    display: flex;
    align-items: center;
  }

  &__contact {
    display: flex;
    flex-direction: column;

    &:not(:first-child) {
      margin-left: 94px;

      @include tablet {
        margin-left: 40px;
      }

      @include mobile {
        margin-left: 16px;
      }
    }
  }

  &__address {
    display: flex;
    font-style: normal;
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__text {
    font-weight: 400;
    font-size: 17px;
    line-height: 130%;
    color: var(--light-color);
  }

  &__text--weight {
    font-weight: 700;
  }

  &__caption {
    display: flex;
    align-items: flex-start;
    font-weight: 700;
    font-size: 15px;
    line-height: 130%;
    color: rgb(255 255 255 / 42%);
  }

  &__link {
    padding-left: 40px;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--el-color);
    }
  }

  &__svg {
    margin-right: 16px;
    width: 24px;
    height: 24px;
    fill: var(--el-color);
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;
  }
}
